<template>
  <div class="home-page">
    <div class="home-container">
      <img src="../assets/images/home_bg@2x.png" />
      <div class="logo-img"><img src="../assets/images/logo@2x.png"></div>
      <div class="home-button">
        <button class="start-button" @click="startPhoto">上传照片</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
    }
  },
  mounted () {
    var url = window.location.href
    if (url.indexOf('?') !== -1) {
      var isLast = this.getQueryString('isLast')
      if (isLast === '1') {
        this.$router.push('/sharephoto')
      }
    }
  },
  methods: {
    // 获取url参数
    getQueryString (name) {
      var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i')
      var r = window.location.search.substr(1).match(reg)
      if (r != null) return decodeURI(r[2])
      return null
    },
    startPhoto () {
      this.$router.push('/choosephoto')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.home-page {
  width: 100%;
  height: 100%;
  overflow: hidden;
  .home-container {
    width: 100%;
    height: 100%;
    line-height: 0;
    img {
      position: relative;
      width: 100%;
      height: 100%;
      z-index: -999;
    }
    .logo-img {
      position: absolute;
      right: 25px;
      top: 25px;
      img {
        width: 100px;
        height: 100%;
      }
    }
    .home-button {
      position: absolute;
      bottom: 65px;
      text-align: center;
      width: 100%;
      // .start-button {
      // }
    }
  }
}
</style>



